export default class File {
  constructor() {
    if($('.state').hasClass('state4')) {
      this.initSelector()
    }
  }

  initSelector() {
    this.$input = $('.input_fileHidden')
    this.$logo = $('.input_fileLogo')
    this.$label = $('.input_fileText')

    this.$input.each((i, e) => {
      let $e = $(e)
      $e.on('change', () => {

        let filesName = ''

        for(let a = 0; a < $e.prop('files').length; a++) {
          if(a != 0) {
            filesName += ", "
          }
          filesName += $e.prop('files')[a].name
        }

        $(this.$label[i]).text(filesName).addClass('input_fileText-valid')
        $(this.$logo[i]).addClass('input_fileLogo-valid')
      })
    })
  }
}
