export default class Validator {
  constructor() {
    this.$input = $('input')
    this.interceptor()
  }

  isEmpty(str) {
    if(str != '') {

      return true
    } else {

      return false
    }
  }

  isString(str) {
    let regexp = /[^0-9]/g

    return regexp.test(str)
  }

  isEmail(str) {
    let regexp = /\S+@\S+\.\S+/

    return regexp.test(str)
  }

  isNumber(str) {
    let regexp = /[^a-zA-Z]/

    return regexp.test(str)
  }

  isValid() {
    this.error = []

    this.$input.each((i, e) => {
      let $e = $(e)
      let val = $e.val()
      let dataTest = $e.attr('data-test')
      let error = ''
      if(dataTest) {
        let test = dataTest.split(' ')
        for(let i in test) {
          if(test[i] == 'string') {
            if(!this.isString(val)) {
              error = 'Format incorrect (caract√®re uniquement)'
            }
          } else if (test[i] == 'number') {
            if(!this.isNumber(val)) {
              error = 'Format incorrect (nombre uniquement)'
            }
          } else if (test[i] == 'email') {
            if(!this.isEmail(val)) {
              error = 'Format incorrect (email uniquement)'
            }
          } else if (test[i] == 'require') {
            if(!this.isEmpty(val)) {
              error = 'Ce champ est requis'
            }
          }
        }
      }
      if(error != '') {
        this.error.push({
          'input' : $e.attr('id'),
          'error' : error
        })
      } else {
        this.error.push({
          'input' : $e.attr('id'),
          'error' : false
        })
      }
    })

    this.showError(this.error)

    for(let i in this.error) {
      if(this.error[i].error) {

        return false
      }
    }

    return true
  }

  showError() {
    $('.error').remove()
    $('.input_file').removeClass('errorBorder')
    for(let i in this.error) {
      $('#' + this.error[i].input).removeClass('errorBorder')
      if(this.error[i].error) {
        if($('#' + this.error[i].input).attr('type') == 'file') {
          $('.input_file[for=' + this.error[i].input + ']').addClass('errorBorder')
        } else {
          $('#' + this.error[i].input).addClass('errorBorder')
        }
        $('#' + this.error[i].input).after('<div class="error">' + this.error[i].error + '</div>')
      }
    }
  }

  refreshInput() {
    this.$input.on('change', () => {
      this.isValid()
    })
  }

  interceptor() {
    $('.state_button').on('click', (e) => {
      this.$input = $('input')
      if(!this.isValid()) {
        e.preventDefault()
        this.refreshInput()
      }
    })
  }
}
