export default class DynamicForm {
  constructor() {
    this.$button = $('.input_moreButton')
    this.clone = 0
    this.listener()
  }

  listener() {
    this.$button.each((i, e) => {
      let $e = $(e)
      $e.on('click', (target) => {
        this.clone++
        this.cloner(target)
      })
    })
  }

  cloner(target) {
    let $target = $(target.target)
    let $container = $target.parent().parent()
    let $input = $container.find('.input_repeater')
    let $clone = $input.slice(0, 1).clone()
    $clone.children().children().each((i, e) => {
      let $e = $(e)
      if($e.attr('id')) {
        $e.attr('id', $e.attr('id') + this.clone)
        $e.val('')
      }
      if($e.attr('label')) {
        $e.attr('label', $e.attr('label') + this.clone)
      }
      if($e.attr('name')) {
        $e.attr('name', $e.attr('name') + this.clone)
      }
      if($e.attr('for')) {
        $e.attr('for', $e.attr('for') + this.clone)
      }
    })
    $clone = $clone.append('<div class="delete"></div>')
    $input.last().after($clone)
    this.deleter()
  }

  deleter() {
    $('.delete').each((i, e) => {
      let $e = $(e)
      $e.on('click', () => {
        $e.parent().remove()
      })
    })
  }

}
